import { shuffle } from '@/utils';

class Scheduler{
    constructor(){
        this.cards = [{question: '', answer: '', _id: ''}]
    }

    loadCards(cards){
        this.cards = shuffle(cards);
        this.i = 0;
    }

    checkOut(id){
        const index = this.findIndex(id);
        this.cards.splice(index, 1);
    }

    findIndex(id){
        for (let i=0; this.cards.length; i++){
            if (this.cards[i]._id === id){
                return i;
            }
        }
        return -1
    }

    getCard(){
        this.i = (this.i + 1)%this.cards.length;
        return this.cards[this.i];
    }
}

export default Scheduler