<template>
    <div class="my-card">
        <div class="card-question">{{this.card_question}}</div>
        <hr>
        <div class="card-answer">{{this.card_answer}}</div>
        <hr>
        <div class="card-nav" v-if="!this.loading">
            <myButton text="Edit" @click="emitEdit"/>
            <myButton text="Erase" @click="emitErase"/>
        </div>
        <div class="card-nav" v-else>
            <div class="spinner"></div>
        </div>
    </div>
</template>

<script>
import myButton from '@/components/myButton.vue'
export default{
    name: 'MyCard',
    data(){
        return {
            loading: false
        }
    },
    props: ['card_question', 'card_answer', 'id'],
    components: {
        myButton,
    },
    methods:{
        emitEdit(){
            this.$emit('edit', this);
        },
        emitErase(){
            this.$emit('erase', this);
        }
    }
}
</script>

<style>
.my-card{
    display: flex;
    flex-direction: column;
    text-align: center;
}
.card-question, .card-answer{
    height: 8rem; 
    overflow: auto;
    padding: 1rem 0.5rem;
}

/* Works on Firefox */
.card-question, .card-answer {
  scrollbar-width: thin;
  scrollbar-color: var(--white) var(--gray);
}

/* Works on Chrome, Edge, and Safari */
.card-question::-webkit-scrollbar, .card-answer::-webkit-scrollbar {
  width: 12px;
}

.card-question::-webkit-scrollbar-track, .card-answer::-webkit-scrollbar {
  background: var(--gray);
}

.card-question::-webkit-scrollbar-thumb, .card-answer::-webkit-scrollbar-thumb {
  background-color: var(--white);
  border-radius: 20px;
  border: 3px solid var(--gray);
}

.card-nav{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
}
.card-nav *{
    margin: 0rem 0.3rem;
}
.my-card hr{
    width: 100%;
    background-color: var(--black);
    color: var(--black);
    border-color: var(--black);
    margin: 0;
}
@media (max-width: 768px) {
}
</style>

