<template>
    <div id="study-view">
        <div class="loading" v-if="this.loading">
            <h1 class="view-title">Study</h1>
            <div class="spinner"></div>
            <div class="text-white" style="text-align:center; margin: 1.5rem;">Retrieving data from the server...</div>
        </div>

        <div v-else>
            <h1 class="view-title">Study: {{this.deckName}} <span style="color: var(--lighter-black)">({{this.totalCards - this.scheduler.cards.length}}/{{this.totalCards}})</span></h1>
            <div id="study-area" v-if="!this.finished">
                <div id="question">
                    {{this.currentCard.question}}
                </div>
                <hr>
                <div id="answer" v-if="this.wasAnswered">
                    {{this.currentCard.answer}}
                </div>
                <hr v-if="this.wasAnswered">
                <div id="study-nav" v-if="!this.wasAnswered">
                    <MyButton text="Show" @click="showAnswer"/>
                </div>
                <div id="study-nav" v-else>
                    <MyButton text="Incorrect" @click="incorrectAnswer"/>
                    <MyButton text="Correct" @click="correctAnswer"/>
                </div>
            </div>
            <div id="study-area" style="padding: 1rem;" v-else>
                <div style="`color:var(--white); text-align: center;`">Congratulations! You finished the <span style="color: var(--lighter-black)">{{this.deckName}}</span> deck!</div>
                <div class="emoji">🎉</div>
                <div style="`color:var(--white); text-align: center;font-size: small;`">Go to <span style="color: var(--lighter-black); font-size: small;">home</span> to start another studying session</div>
                <MyButton id="goHome" text="Home" @click="goHome"/>
            </div>
        </div>
    </div>
</template>

<script>
import { getName, authGetRequest } from '@/utils';
import Scheduler from '@/utils/Scheduler.js'
import MyButton from '@/components/myButton.vue';
export default{
    name: "studyView",
    components: { MyButton },
    data() {
        return {
            username: getName(),
            deckName: "",
            loading: true,
            scheduler: new Scheduler(),
            currentCard: { question: "", answer: "" },
            wasAnswered: false,
            finished: false,
            totalCards: 0
        };
    },
    async mounted() {
        try {
            const response = await authGetRequest(`/deck/${this.$route.params.id}`);
            const data = response.data.data;
            this.scheduler.loadCards(data.cards);
            this.totalCards = data.cards.length;
            this.deckName = data.name;
            this.newCard();
        }
        catch (e) {
            // console.error(e);
        }
        this.loading = false;
    },
    methods: {
        showAnswer(){
            this.wasAnswered = true;
        },
        newCard(){
            const newCard = this.scheduler.getCard();

            if (newCard){
                this.currentCard = newCard;
            }else{
                this.finished = true;
            }
        },
        correctAnswer(){
            this.scheduler.checkOut( this.currentCard._id );
            this.wasAnswered = false;
            this.newCard();
        },
        incorrectAnswer(){
            this.wasAnswered = false;
            this.newCard();
        },
        goHome(){
            this.$router.push({name:'home'});
        }
    },
}
</script>

<style scoped>
#study-area{
    background-color: var(--gray);
    color: white;
    border-radius: 2rem;

    display: flex;
    flex-direction: column;
}
#study-area div{
    padding: 1rem;
    text-align: center;
}
#study-area hr{
    width: 100%;
    margin: 0;
}
#study-nav{
    display: flex;
    align-items: center;
    justify-content: center;
}
#study-nav .my-button{
    margin: 0rem 0.3rem;
}
#goHome{
    margin: auto;
}
</style>