<template>
    <div id="deck-view">
        <div class="loading" v-if="this.loading">
            <h1 class="view-title">{{this.username}}</h1>
            <div class="spinner"></div>
            <div class="text-white" style="text-align:center; margin: 1.5rem;">Retrieving data from the server...</div>
        </div>

        <div v-else>
            <h1 class="view-title">{{this.username}}: {{this.deckName}}</h1>
            <TransitionGroup name="cards" tag="div" class="cards">
                <div class="card-wrapper" v-for="card in this.cards" :key="card._id">
                    <myCard class="card" :card_question="card.question" :card_answer="card.answer" :id="card._id"
                        @erase="erase" @edit="edit"/>
                </div>
                <div class="card-wrapper" key="0" @click="toggleNewCardForm">
                    <div id="new-card">+</div>
                </div>
            </TransitionGroup>

            <newCardModal :active="this.newCardForm" :deck_id="this.$route.params.id" @close="toggleNewCardForm" @addition="addCard"/>
            <editCardModal :active="this.editCardForm" :deck_id="this.$route.params.id" @close="toggleEditCardForm" @update="updateCard"/>
        </div>
    </div>
</template>

<script>
import { authGetRequest, authDeleteRequest, getName } from '@/utils';
import myCard from '@/components/myCard.vue'
import newCardModal from '@/components/newCardModal.vue';
import editCardModal from '@/components/editCardModal.vue';
export default{
    name: 'deckView',
    components:{
    myCard,
    newCardModal,
    editCardModal
},
    data(){
        return {
            loading: true,
            username: getName(),
            deckName: '',
            cards: [],
            newCardForm: false,
            editCardForm: false
        }
    },
    async mounted(){
        try {
            const response = await authGetRequest(`/deck/${this.$route.params.id}`);
            const data = response.data.data;
            this.cards = data.cards;
            this.deckName = data.name;
        } catch (e) {
            // console.error(e);
        }
        this.loading = false;
    },
    methods:{
        async erase(childProxy){
            childProxy.loading = true;
            try{
                const response = await authDeleteRequest(`/card/${childProxy.id}?deck_id=${this.$route.params.id}`);

                // Find the index of the element
                const index = this.cards.findIndex(card => {
                    return card._id === childProxy.id;
                });

                if (index !== -1){
                    // Eliminate the element by index
                    this.cards.splice(index, 1);
                }
            }
            catch(e){
                console.log(e);
            }
            childProxy.loading = false;
        },
        edit(childProxy){
            this.$store.state.currentCardUpdate.question = childProxy.card_question;
            this.$store.state.currentCardUpdate.answer = childProxy.card_answer;
            this.$store.state.currentCardUpdate.id = childProxy.id;
            this.toggleEditCardForm();
        },
        toggleNewCardForm(){
            this.newCardForm = !(this.newCardForm);
        },
        toggleEditCardForm(){
            this.editCardForm = !(this.editCardForm);
        },
        addCard(newCard){
            this.cards.push(newCard);
        },
        updateCard(newCard){
            // / Find the index of the element
            const index = this.cards.findIndex(card => {
                return card._id === newCard._id;
            });

            if (index !== -1){
                this.cards[index] = newCard;
            }
        }
    }
}
</script>

<style scoped>
.cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.cards-move, .cards-enter-active, .cards-leave-active {
  transition: all 0.5s ease;
}
.cards-enter-from, .cards-leave-to {
  opacity: 0;
  transform: scale(0);
}
#new-card, .card{
    background-color: var(--gray);
    border-radius: 2rem;
    margin: 1.5rem 2rem;
    color: white;

    height: 19.25rem;
} 
#new-card{
    display: grid;
    align-items: center;
    justify-items: center;
    font-size: 3rem;
    cursor: pointer;
}
.card-wrapper{
    width: 19rem;
    /* float: left */
}
</style>