<template>
    <div id="new-card-modal" :class="{'active':this.active}">
        <div id="new-card-container">
            <span id="card-modal-x" @click="toggleNewCardForm">&#10006</span>
            <h3 class="view-title" style="margin: 1rem; text-align: center;">New Card</h3>

            <form @submit.prevent="submit">
                <h4 class="view-title subtitle">Question</h4>
                <textarea class="textarea" required v-model="question"></textarea>

                <h4 class="view-title subtitle">Answer</h4>
                <textarea class="textarea" required v-model="answer"></textarea>

                <div id="new-card-nav">
                    <myButton text="Cancel" type="button" @click="toggleNewCardForm"/>
                    <myButton text="Create" type="submit" v-if="!this.loading"/>
                    <div class="spinner" style="margin: 0 1.8rem;" v-else></div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import myButton from '@/components/myButton.vue'
import {authPostRequest} from '@/utils'
export default {
    name: 'NewCardModal',
    props: ['active', 'deck_id'],
    components: {
        myButton
    },
    data(){
        return {
            question: '',
            answer: '',
            loading: false
        }
    },
    methods: {
        toggleNewCardForm(){
            this.$emit('close')
        },
        async submit(){
            this.loading = true;

            const payload = {
                id: this.deck_id,
                question: this.question,
                answer: this.answer
            }

            try{
                const response = await authPostRequest(`/card`, payload);
                const newCard = response.data.data;
                this.$emit('addition', newCard);
                this.$emit('close')

                this.question = '';
                this.answer = '';
                this.loading = false;
            }
            catch(e){
                console.log(e);
            }
        }
    }
}
</script>

<style scoped>
#new-card-modal{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    z-index: 1;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: var(--trans-black);

    visibility:hidden;
}
#new-card-modal.active{
    visibility: visible;
}
#new-card-container{
    width: 20rem;
    background-color: var(--gray);
    border-radius: 2rem;
    padding: 1rem;

    position: relative;

    z-index: 2;
}
#card-modal-x{
    cursor: pointer;
    position: absolute;
    right: 1.5rem;
    top: 1.5rem;
    color: var(--white);
}
.view-title.subtitle{
    font-size:small;
    margin: 1rem;
}
.textarea{
    resize: none;
    width: 100%;
    height: 5rem;
    box-sizing: border-box;
    padding: 0.5rem;
    background-color: var(--black);
    color: var(--white);
    border: 0.15rem solid var(--black);
    border-color: none;
}
.textarea:focus{
    outline: none;
    border-color: var(--lighter-black);
}
#new-card-nav{
    margin-top: 0.5rem;
    display: flex;
    justify-content: flex-end;
}
#new-card-nav *{
    margin-left: 0.6rem;
}

/* Works on Firefox */
.textarea {
  scrollbar-width: thin;
  scrollbar-color: var(--white) var(--black);
}

/* Works on Chrome, Edge, and Safari */
.textarea::-webkit-scrollbar {
  width: 12px;
}

.textarea::-webkit-scrollbar-track{
  background: var(--black);
}

.textarea::-webkit-scrollbar-thumb {
  background-color: var(--white);
  border-radius: 20px;
  border: 3px solid var(--black);
}
</style>