<template>
    <div id="register">
        <h1 class="view-title">Register</h1>
        <form class="container" @submit.prevent="submit" v-if="!this.success">
            <div class="my-input-group">
                <div class="my-input-desc">Name</div>
                <input v-model="name" class="my-input" type="text" required>
            </div>
            <div class="my-input-group">
                <div class="my-input-desc">Email</div>
                <input v-model="email" class="my-input" type="email" required>
            </div>
            <div class="my-input-group">
                <div class="my-input-desc">Password</div>
                <input v-model="password" class="my-input" type="password" required>
            </div>
            <div class="my-input-group">
                <div class="my-input-desc">Confirm Password</div>
                <input v-model="passwordConfirmation" class="my-input" type="password" 
                    ref="passwordConfirmation" required>
            </div>

            <div v-if="message" :style="`color:red; text-align: center; margin-bottom: 0.5rem;`">{{this.message}}</div>

            <input type="checkbox" required v-model="recaptchaVerified" style="display: none;">
            <vue-recaptcha ref="recaptcha" 
                :sitekey="this.$store.state.captchaKey" style="margin: 1rem auto;"
                @verify="verifyCaptcha"/>

            <myButton text="Register" type="submit" v-if="!this.loading"/>
            <div class="spinner" v-else></div>
        </form>

        <div class="container" v-else>
            <div class="emoji">🙌</div>
            <div :style="`color:white; text-align: center; margin:1rem;`">The user was created successfully!</div>
            <router-link :to="{name:'login'}" style="text-align: center;">
                <myButton text="Login"/>
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import myButton from '@/components/myButton.vue'
import { VueRecaptcha } from 'vue-recaptcha';

export default {
    name: 'register',
    components: {
        myButton,
        VueRecaptcha,
    },
    data(){
        return {
            name: '',
            email: '',
            password: '',
            passwordConfirmation: '',
            loading: false,
            message: undefined,
            success: false,
            recaptchaVerified: false,
        }
    },
    methods:{
        async submit(){
            // Check if the passwords are the same
            if (this.password !== this.passwordConfirmation){
                this.message = 'Passwords do not match!'
                return
            }

            const baseURL = this.$store.state.apiURL;
            const payload = {name: this.name, email: this.email, password: this.password};
            this.loading = true;
            try {
                const response = await axios.post(`${baseURL}/user/register`, payload);
            
                if (response.status === 201){
                    this.name='';this.email='';this.password = '';this.passwordConfirmation='';
                    this.success = true;
                    return;
                }

            } catch (e) {
                const response = e.response;
                this.message = response.data.message;
            }
            this.loading = false;
            this.password = '';
            this.passwordConfirmation = '';
        },
        verifyCaptcha(){
            this.recaptchaVerified = true;
        }
    }
}
</script>

<style scoped>
.container{
    background-color: var(--gray);
    max-width: 30rem;
    margin: auto;
    padding: 1.5rem;
    border-radius: 2rem;

    display: flex;
    flex-direction: column;
}
.container .my-input-group{
    margin: 1rem;
}
.container .my-button{
    margin: 0.5rem auto 0rem;
}
.view-title{
    margin: 0 inherit;
    text-align: center;
}

.my-input-group{
    display: flex;
    flex-direction: row;
}
.my-input-group .my-input-desc{
    flex: 0 1 10rem;
    color: var(--white);
}

.emoji{
    font-size: 5rem;
    text-align: center;
}

@media (max-width: 768px) {
    .container{
        padding: 1rem;
    }

    .my-input-group{
        flex-direction: column;
    }
    .my-input-group .my-input-desc{
        flex: 1;
        color: var(--white);
        margin-bottom: 1rem;
        text-align: center;
    }
    .my-input{
        flex: 1;
    }
}
</style>